import axios from "axios"
import { load } from "cheerio"

export const solidgateParser = async (getLink, testVacancies) => {
	const vacancies = []
	try {
		const res = await axios.get(getLink('Solidgate'))
		const $ = load(res.data)
		$('li.position').each((i, elem) => {
			const company = 'Solidgate'
			const title = $(elem).find('h2').text()?.replaceAll('\n', '').trim();
			const link = 'https://solid.breezy.hr' + $(elem).find('a').attr('href')
			const city = 'Ukraine'
			const date = '';
			if (testVacancies(title)) vacancies.push({ company, title, link, city, date })

		});
	} catch (error) {
		console.log(error);
	}
	return vacancies
}